nav {
  z-index: 100;
  position: relative;
}

nav .navigation {
  color: #f3d70d;
  text-transform: capitalize;
  background: #b02d47;
  border-radius: .2rem;
  flex-direction: row;
  justify-content: flex-start;
  align-items: center;
  gap: 3rem;
  width: 70%;
