  font-family: Montserrat;
  font-size: 1.6rem;
  font-weight: 400;
  list-style: none;
  transition: all .5s;
  display: flex;
  position: absolute;
}

nav .navigation .navigation-list {
  cursor: pointer;
  position: relative;
  overflow: hidden;
}

nav .navigation .navigation-list:after {
  content: "";
  transform-origin: 0;
  background-color: #f3d70d;
  width: 100%;
  height: 5%;
  transition: all .3s;
  position: absolute;
  bottom: 0;
  left: 0;
  transform: translateX(-15rem);
}

nav .navigation .navigation-list:hover {
  opacity: .8;
}

nav .navigation .navigation-list:hover:after {
  transform: translateX(0) !important;
}

nav .navigation-menu-btn {
  display: none;
  position: absolute;
  top: 0;
  left: 1rem;
  transform: scale(.6);
}

@media only screen and (width <= 768px) {
  nav {
    height: 100%;
    transition: all .4s;
    display: inline-block;
    left: 0;
    position: fixed !important;
    top: 0 !important;
  }

  nav .navigation-menu-btn {
    cursor: pointer;
    z-index: 10;
    display: inline-block;
  }

  nav .navigation {
    transform-origin: 0;
    flex-direction: column;
    justify-content: center;
    gap: 2rem;
    width: 70%;
    height: 100vh;
    font-size: 3rem;
    transform: translateX(-300%);
    transition: all .5s !important;
  }

  nav .navigation .navigation-list {
    transition: none !important;
  }

  nav .navigation .navigation-list:after {
    display: none;
  }

  nav .navigation .navigation-list:active {
    transform: translateY(-.3rem);
  }
}

